
.map {
  position: relative;
  &:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  background: url('/images/main/line.png')no-repeat;
  background-size: contain;
  width: 300px;
  height: 300px;
  z-index: 1;
  }
  &:after {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  background: url('/images/main/line1.png')no-repeat;
  background-size: contain;
  width: 250px;
  height: 400px;
  z-index: 1;
  }
  &__info {
    position: absolute;
    z-index: 1;
    max-width: 300px;
    width: 100%;
    left: 20%;
    top: 0;
    background-color: rgba(0,0,0, .85);
    color: #fff;
    padding: 10px;

    &__logotipe {
      margin-bottom: 50px;
    }

  }
  .map-box {
    position: relative;
    height: 500px;
  }
}
